import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Subscription } from '../../types';

interface SubscriptionState {
  subscription: Subscription | null;
  loading: boolean;
  error: string | null;
  successMessage: string | null;
}

const initialState: SubscriptionState = {
  subscription: null,
  loading: false,
  error: null,
  successMessage: null,
};

const subscriptionSlice = createSlice({
  name: 'subscription',
  initialState,
  reducers: {
    subscribeStart: (state) => {
      state.loading = true;
      state.error = null;
      state.successMessage = null;
    },
    subscribeSuccess: (state, action: PayloadAction<{ subscription: Subscription; message: string }>) => {
      state.loading = false;
      state.subscription = action.payload.subscription;
      state.successMessage = action.payload.message;
      state.error = null;
    },
    subscribeFailure: (state, action: PayloadAction<string>) => {
      state.loading = false;
      state.error = action.payload;
      state.successMessage = null;
    },
    updateSubscriptionStart: (state) => {
      state.loading = true;
      state.error = null;
      state.successMessage = null;
    },
    updateSubscriptionSuccess: (state, action: PayloadAction<{ subscription: Subscription; message: string }>) => {
      state.loading = false;
      state.subscription = action.payload.subscription;
      state.successMessage = action.payload.message;
      state.error = null;
    },
    updateSubscriptionFailure: (state, action: PayloadAction<string>) => {
      state.loading = false;
      state.error = action.payload;
      state.successMessage = null;
    },
    unsubscribeStart: (state) => {
      state.loading = true;
      state.error = null;
      state.successMessage = null;
    },
    unsubscribeSuccess: (state, action: PayloadAction<string>) => {
      state.loading = false;
      state.subscription = null;
      state.successMessage = action.payload;
      state.error = null;
    },
    unsubscribeFailure: (state, action: PayloadAction<string>) => {
      state.loading = false;
      state.error = action.payload;
      state.successMessage = null;
    },
    clearMessages: (state) => {
      state.error = null;
      state.successMessage = null;
    },
    setSubscription: (state, action: PayloadAction<Subscription>) => {
      state.subscription = action.payload;
    },
  },
});

export const {
  subscribeStart,
  subscribeSuccess,
  subscribeFailure,
  updateSubscriptionStart,
  updateSubscriptionSuccess,
  updateSubscriptionFailure,
  unsubscribeStart,
  unsubscribeSuccess,
  unsubscribeFailure,
  clearMessages,
  setSubscription,
} = subscriptionSlice.actions;

export default subscriptionSlice.reducer;