import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Card,
  CardContent,
  Grid,
  Chip,
  Button,
  Breadcrumbs,
  Link,
  Paper,
  TextField,
  InputAdornment,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Skeleton,
} from '@mui/material';
import {
  Search as SearchIcon,
  NavigateNext as NavigateNextIcon,
  LibraryBooks as JournalIcon,
  TrendingUp as ImpactIcon,
  DateRange as DateIcon,
} from '@mui/icons-material';
import { useNavigate, useParams, useSearchParams } from 'react-router-dom';

interface Journal {
  id: string;
  name: string;
  description: string;
  impactFactor: number;
  issn: string;
  subjects: string[];
  totalArticles: number;
  currentVolume: number;
  currentIssue: number;
  coverImage?: string;
}

const JournalsPage: React.FC = () => {
  const navigate = useNavigate();
  const { journalId, year, volume, issue } = useParams();
  const [searchParams] = useSearchParams();
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedSubject, setSelectedSubject] = useState('');

  // Mock data - replace with actual API calls
  const [journals] = useState<Journal[]>([
    {
      id: 'skillmate-research',
      name: 'Skillmate Research Journal',
      description: 'Peer-reviewed journal focusing on cutting-edge research in AI, ML, and Data Science',
      impactFactor: 4.2,
      issn: '2456-7890',
      subjects: ['Artificial Intelligence', 'Machine Learning', 'Data Science'],
      totalArticles: 456,
      currentVolume: 15,
      currentIssue: 2,
    },
    {
      id: 'skillmate-technology',
      name: 'Skillmate Technology Review',
      description: 'Advanced research in software engineering, cybersecurity, and emerging technologies',
      impactFactor: 3.8,
      issn: '2456-7891',
      subjects: ['Software Engineering', 'Cybersecurity', 'Blockchain'],
      totalArticles: 234,
      currentVolume: 12,
      currentIssue: 4,
    },
    {
      id: 'skillmate-medical',
      name: 'Skillmate Medical Sciences',
      description: 'Medical research with focus on AI applications in healthcare and diagnostics',
      impactFactor: 5.1,
      issn: '2456-7892',
      subjects: ['Medical AI', 'Healthcare Technology', 'Biomedical Engineering'],
      totalArticles: 189,
      currentVolume: 8,
      currentIssue: 3,
    },
    {
      id: 'skillmate-engineering',
      name: 'Skillmate Engineering Advances',
      description: 'Engineering research covering robotics, automation, and smart systems',
      impactFactor: 3.5,
      issn: '2456-7893',
      subjects: ['Robotics', 'Automation', 'IoT'],
      totalArticles: 167,
      currentVolume: 10,
      currentIssue: 1,
    },
  ]);

  const [availableYears] = useState([2024, 2023, 2022, 2021, 2020]);
  const [availableSubjects] = useState([
    'All Subjects',
    'Artificial Intelligence',
    'Machine Learning',
    'Data Science',
    'Software Engineering',
    'Cybersecurity',
    'Medical AI',
    'Robotics',
    'Blockchain',
    'IoT',
  ]);

  useEffect(() => {
    // Simulate loading
    setTimeout(() => setLoading(false), 800);
  }, []);

  const filteredJournals = journals.filter(journal => {
    const matchesSearch = !searchQuery || 
      journal.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      journal.description.toLowerCase().includes(searchQuery.toLowerCase());
    
    const matchesSubject = !selectedSubject || selectedSubject === 'All Subjects' ||
      journal.subjects.some(subject => subject === selectedSubject);
    
    return matchesSearch && matchesSubject;
  });

  const handleJournalClick = (journalId: string) => {
    navigate(`/journals/${journalId}`);
  };

  const handleYearClick = (journalId: string, year: number) => {
    navigate(`/journals/${journalId}/${year}`);
  };

  if (loading) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: 'repeat(2, 1fr)' }, gap: 3 }}>
          {[1, 2, 3, 4].map((i) => (
            <Card key={i}>
              <CardContent>
                <Skeleton variant="text" width="80%" height={32} />
                <Skeleton variant="text" width="60%" height={24} />
                <Skeleton variant="rectangular" height={100} sx={{ my: 2 }} />
                <Box sx={{ display: 'flex', gap: 1 }}>
                  <Skeleton variant="rounded" width={80} height={24} />
                  <Skeleton variant="rounded" width={100} height={24} />
                </Box>
              </CardContent>
            </Card>
          ))}
        </Box>
      </Container>
    );
  }

  // Show specific journal's year navigation
  if (journalId && !year) {
    const currentJournal = journals.find(j => j.id === journalId);
    if (!currentJournal) {
      return <div>Journal not found</div>;
    }

    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Breadcrumbs separator={<NavigateNextIcon fontSize="small" />} sx={{ mb: 4 }}>
          <Link onClick={() => navigate('/journals')} sx={{ cursor: 'pointer' }}>
            Journals
          </Link>
          <Typography color="text.primary">{currentJournal.name}</Typography>
        </Breadcrumbs>

        <Box sx={{ mb: 4 }}>
          <Typography variant="h3" gutterBottom sx={{ fontWeight: 'bold' }}>
            {currentJournal.name}
          </Typography>
          <Typography variant="h6" color="text.secondary" paragraph>
            {currentJournal.description}
          </Typography>
          
          <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap', mb: 3 }}>
            <Chip icon={<ImpactIcon />} label={`Impact Factor: ${currentJournal.impactFactor}`} color="primary" />
            <Chip label={`ISSN: ${currentJournal.issn}`} variant="outlined" />
            <Chip label={`${currentJournal.totalArticles} Articles`} variant="outlined" />
          </Box>
        </Box>

        <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold', mb: 3 }}>
          Browse by Year
        </Typography>

        <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', sm: 'repeat(2, 1fr)', md: 'repeat(3, 1fr)' }, gap: 3 }}>
          {availableYears.map((yearOption) => (
            <Card 
              key={yearOption} 
              sx={{ 
                cursor: 'pointer',
                transition: 'all 0.3s ease',
                '&:hover': {
                  transform: 'translateY(-4px)',
                  boxShadow: 4,
                }
              }}
              onClick={() => handleYearClick(journalId, yearOption)}
            >
              <CardContent sx={{ textAlign: 'center', py: 4 }}>
                <DateIcon sx={{ fontSize: 48, color: 'primary.main', mb: 2 }} />
                <Typography variant="h4" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
                  {yearOption}
                </Typography>
                <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                  {Math.floor(Math.random() * 50) + 20} Articles
                </Typography>
              </CardContent>
            </Card>
          ))}
        </Box>
      </Container>
    );
  }

  // Show volumes for a specific year
  if (journalId && year && !volume) {
    const currentJournal = journals.find(j => j.id === journalId);
    if (!currentJournal) {
      return <div>Journal not found</div>;
    }

    // Mock volumes data
    const volumes = [
      { number: 1, issueCount: 4, articles: 45 },
      { number: 2, issueCount: 4, articles: 52 },
      { number: 3, issueCount: 3, articles: 38 },
    ];

    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Breadcrumbs separator={<NavigateNextIcon fontSize="small" />} sx={{ mb: 4 }}>
          <Link onClick={() => navigate('/journals')} sx={{ cursor: 'pointer' }}>
            Journals
          </Link>
          <Link onClick={() => navigate(`/journals/${journalId}`)} sx={{ cursor: 'pointer' }}>
            {currentJournal.name}
          </Link>
          <Typography color="text.primary">{year}</Typography>
        </Breadcrumbs>

        <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
          Volumes for {year}
        </Typography>

        <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: 'repeat(2, 1fr)' }, gap: 3, mt: 3 }}>
          {volumes.map((vol) => (
            <Card 
              key={vol.number}
              sx={{ 
                cursor: 'pointer',
                '&:hover': { boxShadow: 4 }
              }}
              onClick={() => navigate(`/journals/${journalId}/${year}/${vol.number}`)}
            >
              <CardContent>
                <Typography variant="h5" sx={{ fontWeight: 'bold', mb: 2 }}>
                  Volume {vol.number}
                </Typography>
                <Typography variant="body2" color="text.secondary" gutterBottom>
                  {vol.issueCount} Issues • {vol.articles} Articles
                </Typography>
                <Button variant="outlined" sx={{ mt: 2 }}>
                  Browse Issues
                </Button>
              </CardContent>
            </Card>
          ))}
        </Box>
      </Container>
    );
  }

  // Show issues for a specific volume
  if (journalId && year && volume && !issue) {
    const currentJournal = journals.find(j => j.id === journalId);
    if (!currentJournal) {
      return <div>Journal not found</div>;
    }

    // Mock issues data
    const issues = [
      { 
        number: 1, 
        title: 'Special Issue: AI in Healthcare', 
        publishedDate: '2024-03-15',
        articles: 12,
        isSpecialIssue: true,
        coverImage: null
      },
      { 
        number: 2, 
        title: 'Regular Issue', 
        publishedDate: '2024-06-15',
        articles: 15,
        isSpecialIssue: false,
        coverImage: null
      },
      { 
        number: 3, 
        title: 'Special Issue: Machine Learning Applications', 
        publishedDate: '2024-09-15',
        articles: 10,
        isSpecialIssue: true,
        coverImage: null
      },
    ];

    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Breadcrumbs separator={<NavigateNextIcon fontSize="small" />} sx={{ mb: 4 }}>
          <Link onClick={() => navigate('/journals')} sx={{ cursor: 'pointer' }}>
            Journals
          </Link>
          <Link onClick={() => navigate(`/journals/${journalId}`)} sx={{ cursor: 'pointer' }}>
            {currentJournal.name}
          </Link>
          <Link onClick={() => navigate(`/journals/${journalId}/${year}`)} sx={{ cursor: 'pointer' }}>
            {year}
          </Link>
          <Typography color="text.primary">Volume {volume}</Typography>
        </Breadcrumbs>

        <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
          Volume {volume} Issues
        </Typography>

        <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: 'repeat(2, 1fr)' }, gap: 3, mt: 3 }}>
          {issues.map((iss) => (
            <Card 
              key={iss.number}
              sx={{ 
                cursor: 'pointer',
                border: iss.isSpecialIssue ? '2px solid' : '1px solid',
                borderColor: iss.isSpecialIssue ? 'primary.main' : 'divider',
                '&:hover': { boxShadow: 4 }
              }}
              onClick={() => navigate(`/journals/${journalId}/${year}/${volume}/${iss.number}`)}
            >
              <CardContent>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 2 }}>
                  <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                    Issue {iss.number}
                  </Typography>
                  {iss.isSpecialIssue && (
                    <Chip label="Special Issue" color="primary" size="small" />
                  )}
                </Box>
                <Typography variant="subtitle1" gutterBottom>
                  {iss.title}
                </Typography>
                <Typography variant="body2" color="text.secondary" gutterBottom>
                  Published: {new Date(iss.publishedDate).toLocaleDateString()}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  {iss.articles} Articles
                </Typography>
                <Button variant="outlined" sx={{ mt: 2 }}>
                  View Articles
                </Button>
              </CardContent>
            </Card>
          ))}
        </Box>
      </Container>
    );
  }

  // Main journals listing page
  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Box sx={{ mb: 4 }}>
        <Typography variant="h3" component="h1" gutterBottom sx={{ fontWeight: 'bold' }}>
          Browse Journals
        </Typography>
        <Typography variant="h6" color="text.secondary" paragraph>
          Discover cutting-edge research across multiple disciplines
        </Typography>
      </Box>

      {/* Search and Filter */}
      <Paper sx={{ p: 3, mb: 4 }}>
        <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: '2fr 1fr' }, gap: 2 }}>
          <TextField
            fullWidth
            placeholder="Search journals..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon />
                </InputAdornment>
              ),
            }}
          />
          <FormControl fullWidth>
            <InputLabel>Subject Area</InputLabel>
            <Select
              value={selectedSubject}
              label="Subject Area"
              onChange={(e) => setSelectedSubject(e.target.value)}
            >
              {availableSubjects.map((subject) => (
                <MenuItem key={subject} value={subject}>
                  {subject}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Box>
      </Paper>

      {/* Journals Grid */}
      <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: 'repeat(2, 1fr)' }, gap: 4 }}>
        {filteredJournals.map((journal) => (
          <Card 
            key={journal.id}
            sx={{ 
              height: '100%',
              cursor: 'pointer',
              transition: 'all 0.3s ease',
              '&:hover': {
                transform: 'translateY(-4px)',
                boxShadow: 6,
              }
            }}
            onClick={() => handleJournalClick(journal.id)}
          >
            <CardContent sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
              <Box sx={{ display: 'flex', alignItems: 'flex-start', mb: 2 }}>
                <JournalIcon sx={{ fontSize: 40, color: 'primary.main', mr: 2, mt: 0.5 }} />
                <Box sx={{ flex: 1 }}>
                  <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold', lineHeight: 1.2 }}>
                    {journal.name}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    ISSN: {journal.issn}
                  </Typography>
                </Box>
              </Box>

              <Typography variant="body1" paragraph sx={{ flex: 1 }}>
                {journal.description}
              </Typography>

              <Box sx={{ mb: 3 }}>
                <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap', mb: 2 }}>
                  {journal.subjects.slice(0, 3).map((subject) => (
                    <Chip 
                      key={subject} 
                      label={subject} 
                      size="small" 
                      variant="outlined"
                      color="primary"
                    />
                  ))}
                  {journal.subjects.length > 3 && (
                    <Chip 
                      label={`+${journal.subjects.length - 3} more`} 
                      size="small" 
                      variant="outlined"
                    />
                  )}
                </Box>
              </Box>

              <Box sx={{ 
                display: 'flex', 
                justifyContent: 'space-between',
                alignItems: 'center',
                pt: 2,
                borderTop: '1px solid',
                borderColor: 'divider'
              }}>
                <Box sx={{ display: 'flex', gap: 3 }}>
                  <Box sx={{ textAlign: 'center' }}>
                    <Typography variant="h6" color="primary" sx={{ fontWeight: 'bold' }}>
                      {journal.impactFactor}
                    </Typography>
                    <Typography variant="caption" color="text.secondary">
                      Impact Factor
                    </Typography>
                  </Box>
                  <Box sx={{ textAlign: 'center' }}>
                    <Typography variant="h6" color="primary" sx={{ fontWeight: 'bold' }}>
                      {journal.totalArticles}
                    </Typography>
                    <Typography variant="caption" color="text.secondary">
                      Articles
                    </Typography>
                  </Box>
                </Box>
                <Button variant="contained" size="small">
                  Browse
                </Button>
              </Box>
            </CardContent>
          </Card>
        ))}
      </Box>

      {filteredJournals.length === 0 && (
        <Paper sx={{ p: 6, textAlign: 'center' }}>
          <JournalIcon sx={{ fontSize: 64, color: 'text.secondary', mb: 2 }} />
          <Typography variant="h6" color="text.secondary">
            No journals found matching your criteria
          </Typography>
          <Button 
            variant="outlined" 
            sx={{ mt: 2 }}
            onClick={() => {
              setSearchQuery('');
              setSelectedSubject('');
            }}
          >
            Clear Filters
          </Button>
        </Paper>
      )}
    </Container>
  );
};

export default JournalsPage;