// Utility functions (inline for now)
const formatNumber = (num: number): string => {
  if (num >= 1000000) return (num / 1000000).toFixed(1) + 'M';
  if (num >= 1000) return (num / 1000).toFixed(1) + 'K';
  return num.toString();
};

const formatAuthorNames = (authors: Array<{ firstName: string; lastName: string }>): string => {
  if (authors.length === 0) return '';
  if (authors.length === 1) return `${authors[0].firstName} ${authors[0].lastName}`;
  if (authors.length === 2) {
    return `${authors[0].firstName} ${authors[0].lastName} and ${authors[1].firstName} ${authors[1].lastName}`;
  }
  return `${authors[0].firstName} ${authors[0].lastName} et al.`;
};

