import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { SearchFilters, SearchResult, Article } from '../../types';

interface SearchState {
  query: string;
  filters: SearchFilters;
  results: Article[];
  loading: boolean;
  error: string | null;
  totalResults: number;
  currentPage: number;
  resultsPerPage: number;
  suggestions: string[];
}

const initialState: SearchState = {
  query: '',
  filters: {},
  results: [],
  loading: false,
  error: null,
  totalResults: 0,
  currentPage: 1,
  resultsPerPage: 10,
  suggestions: [],
};

const searchSlice = createSlice({
  name: 'search',
  initialState,
  reducers: {
    setQuery: (state, action: PayloadAction<string>) => {
      state.query = action.payload;
    },
    setFilters: (state, action: PayloadAction<SearchFilters>) => {
      state.filters = { ...state.filters, ...action.payload };
    },
    clearFilters: (state) => {
      state.filters = {};
    },
    searchStart: (state) => {
      state.loading = true;
      state.error = null;
    },
    searchSuccess: (state, action: PayloadAction<SearchResult>) => {
      state.loading = false;
      state.results = action.payload.articles;
      state.totalResults = action.payload.totalCount;
      state.currentPage = action.payload.page;
      state.resultsPerPage = action.payload.limit;
      state.error = null;
    },
    searchFailure: (state, action: PayloadAction<string>) => {
      state.loading = false;
      state.error = action.payload;
    },
    setSuggestions: (state, action: PayloadAction<string[]>) => {
      state.suggestions = action.payload;
    },
    clearSearch: (state) => {
      state.query = '';
      state.filters = {};
      state.results = [];
      state.totalResults = 0;
      state.currentPage = 1;
      state.error = null;
      state.suggestions = [];
    },
    setCurrentPage: (state, action: PayloadAction<number>) => {
      state.currentPage = action.payload;
    },
  },
});

export const {
  setQuery,
  setFilters,
  clearFilters,
  searchStart,
  searchSuccess,
  searchFailure,
  setSuggestions,
  clearSearch,
  setCurrentPage,
} = searchSlice.actions;

export default searchSlice.reducer;