import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Article, ArticleMetrics } from '../../types';

interface ArticlesState {
  articles: Article[];
  currentArticle: Article | null;
  featuredArticles: Article[];
  mostCitedArticles: Article[];
  relatedArticles: Article[];
  loading: boolean;
  error: string | null;
  pagination: {
    page: number;
    limit: number;
    total: number;
  };
}

const initialState: ArticlesState = {
  articles: [],
  currentArticle: null,
  featuredArticles: [],
  mostCitedArticles: [],
  relatedArticles: [],
  loading: false,
  error: null,
  pagination: {
    page: 1,
    limit: 10,
    total: 0,
  },
};

const articlesSlice = createSlice({
  name: 'articles',
  initialState,
  reducers: {
    fetchArticlesStart: (state) => {
      state.loading = true;
      state.error = null;
    },
    fetchArticlesSuccess: (state, action: PayloadAction<{ articles: Article[]; total: number; page: number }>) => {
      state.loading = false;
      state.articles = action.payload.articles;
      state.pagination.total = action.payload.total;
      state.pagination.page = action.payload.page;
      state.error = null;
    },
    fetchArticlesFailure: (state, action: PayloadAction<string>) => {
      state.loading = false;
      state.error = action.payload;
    },
    setCurrentArticle: (state, action: PayloadAction<Article>) => {
      state.currentArticle = action.payload;
    },
    setFeaturedArticles: (state, action: PayloadAction<Article[]>) => {
      state.featuredArticles = action.payload;
    },
    setMostCitedArticles: (state, action: PayloadAction<Article[]>) => {
      state.mostCitedArticles = action.payload;
    },
    setRelatedArticles: (state, action: PayloadAction<Article[]>) => {
      state.relatedArticles = action.payload;
    },
    updateArticleMetrics: (state, action: PayloadAction<{ articleId: string; metrics: ArticleMetrics }>) => {
      const article = state.articles.find(a => a.id === action.payload.articleId);
      if (article) {
        article.views = action.payload.metrics.views;
        article.downloads = action.payload.metrics.downloads;
        article.citations = action.payload.metrics.citations;
      }
      if (state.currentArticle && state.currentArticle.id === action.payload.articleId) {
        state.currentArticle.views = action.payload.metrics.views;
        state.currentArticle.downloads = action.payload.metrics.downloads;
        state.currentArticle.citations = action.payload.metrics.citations;
      }
    },
    clearCurrentArticle: (state) => {
      state.currentArticle = null;
      state.relatedArticles = [];
    },
    setPagination: (state, action: PayloadAction<{ page: number; limit: number }>) => {
      state.pagination.page = action.payload.page;
      state.pagination.limit = action.payload.limit;
    },
  },
});

export const {
  fetchArticlesStart,
  fetchArticlesSuccess,
  fetchArticlesFailure,
  setCurrentArticle,
  setFeaturedArticles,
  setMostCitedArticles,
  setRelatedArticles,
  updateArticleMetrics,
  clearCurrentArticle,
  setPagination,
} = articlesSlice.actions;

export default articlesSlice.reducer;