import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  Paper,

  TextField,
  Button,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Chip,
  Alert,
  CircularProgress,
  MenuItem,
  FormControl,
  InputLabel,
  Select,
  Accordion,
  AccordionSummary,
  AccordionDetails,
} from '@mui/material';
import {
  LocationOn as LocationIcon,
  Email as EmailIcon,
  Phone as PhoneIcon,
  Business as BusinessIcon,
  Send as SendIcon,
  Person as PersonIcon,
  QuestionAnswer as QuestionIcon,
  Schedule as ScheduleIcon,
  Support as SupportIcon,
  Description as DocumentIcon,
  School as AcademicIcon,
  ExpandMore as ExpandMoreIcon,
} from '@mui/icons-material';
import { useForm } from 'react-hook-form';

interface ContactFormData {
  name: string;
  email: string;
  subject: string;
  category: string;
  message: string;
  organization?: string;
}

const ContactPage: React.FC = () => {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm<ContactFormData>();

  const contactCategories = [
    { value: 'general', label: 'General Inquiry' },
    { value: 'submission', label: 'Article Submission' },
    { value: 'review', label: 'Peer Review' },
    { value: 'subscription', label: 'Subscription & Access' },
    { value: 'technical', label: 'Technical Support' },
    { value: 'editorial', label: 'Editorial Inquiry' },
    { value: 'partnership', label: 'Partnership & Collaboration' },
    { value: 'other', label: 'Other' },
  ];

  const onSubmit = async (data: ContactFormData) => {
    setIsSubmitting(true);
    
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      console.log('Contact form submission:', data);
      
      setSubmitSuccess(true);
      reset();
    } catch (error) {
      console.error('Contact form error:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  const faqs = [
    {
      question: 'How do I submit an article for publication?',
      answer: 'You can submit articles through our online submission system. Please visit our "For Authors" section for detailed guidelines, formatting requirements, and submission procedures.',
    },
    {
      question: 'What is the typical review process timeline?',
      answer: 'The peer review process typically takes 4-8 weeks. Initial editorial screening takes 1-2 weeks, followed by peer review (3-6 weeks), and final editorial decision (1 week).',
    },
    {
      question: 'How can I access published articles?',
      answer: 'Many of our articles are available through open access. For subscription-based content, you can subscribe to our journals or access through institutional subscriptions.',
    },
    {
      question: 'Can I become a peer reviewer?',
      answer: 'Yes! We welcome qualified researchers to join our peer review network. Please contact us with your CV and areas of expertise.',
    },
    {
      question: 'Do you offer institutional partnerships?',
      answer: 'Absolutely. We offer various partnership opportunities including institutional subscriptions, special issues, and collaborative publishing agreements.',
    },
  ];

  const officeHours = [
    { day: 'Monday - Friday', time: '9:00 AM - 6:00 PM IST' },
    { day: 'Saturday', time: '10:00 AM - 2:00 PM IST' },
    { day: 'Sunday', time: 'Closed' },
  ];

  if (submitSuccess) {
    return (
      <Container maxWidth="md" sx={{ py: 6 }}>
        <Paper sx={{ p: 6, textAlign: 'center' }}>
          <SendIcon sx={{ fontSize: 80, color: 'success.main', mb: 3 }} />
          <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
            Message Sent Successfully!
          </Typography>
          <Typography variant="body1" paragraph color="text.secondary">
            Thank you for contacting us. We have received your message and will respond within 24-48 hours.
          </Typography>
          <Typography variant="body2" color="text.secondary" paragraph>
            For urgent inquiries, please call us directly at +91 6280713069.
          </Typography>
          <Button
            variant="contained"
            size="large"
            onClick={() => setSubmitSuccess(false)}
            sx={{ mt: 2 }}
          >
            Send Another Message
          </Button>
        </Paper>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      {/* Header Section */}
      <Box sx={{ textAlign: 'center', mb: 6 }}>
        <Typography variant="h3" gutterBottom sx={{ fontWeight: 'bold' }}>
          Contact Us
        </Typography>
        <Typography variant="h6" color="text.secondary" paragraph>
          Get in touch with our team for any inquiries, support, or collaboration opportunities
        </Typography>
      </Box>

      <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', lg: '1fr 1fr' }, gap: 4, mb: 6 }}>
        {/* Contact Information */}
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
          {/* Company Details */}
          <Paper sx={{ p: 4 }}>
            <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold', mb: 3 }}>
              Contact Information
            </Typography>
            
            <List>
              <ListItem>
                <ListItemIcon>
                  <BusinessIcon color="primary" />
                </ListItemIcon>
                <ListItemText
                  primary="Company"
                  secondary="SKILLMATE WORLD PRIVATE LIMITED"
                />
              </ListItem>
              
              <ListItem>
                <ListItemIcon>
                  <PersonIcon color="primary" />
                </ListItemIcon>
                <ListItemText
                  primary="Contact Person"
                  secondary="Dr. S. Chaturvedi"
                />
              </ListItem>
              
              <ListItem>
                <ListItemIcon>
                  <EmailIcon color="primary" />
                </ListItemIcon>
                <ListItemText
                  primary="Email"
                  secondary="contact@skillmate.org.in"
                />
              </ListItem>
              
              <ListItem>
                <ListItemIcon>
                  <PhoneIcon color="primary" />
                </ListItemIcon>
                <ListItemText
                  primary="Phone"
                  secondary="+91 6280713069"
                />
              </ListItem>
              
              <ListItem>
                <ListItemIcon>
                  <LocationIcon color="primary" />
                </ListItemIcon>
                <ListItemText
                  primary="Address"
                  secondary="16, AKASH ENCLAVE COLONY, BYPASS, Krishna Nagar (Mathura), Mathura, Mathura- 281004, Uttar Pradesh, India"
                />
              </ListItem>
            </List>
          </Paper>

          {/* Office Hours */}
          <Paper sx={{ p: 4 }}>
            <Typography variant="h6" gutterBottom sx={{ fontWeight: 'bold', display: 'flex', alignItems: 'center', gap: 1 }}>
              <ScheduleIcon color="primary" />
              Office Hours
            </Typography>
            
            <List>
              {officeHours.map((schedule, index) => (
                <ListItem key={index} sx={{ px: 0 }}>
                  <ListItemText
                    primary={schedule.day}
                    secondary={schedule.time}
                  />
                </ListItem>
              ))}
            </List>

            <Alert severity="info" sx={{ mt: 2 }}>
              <Typography variant="body2">
                For urgent matters outside office hours, please send an email and we'll respond as soon as possible.
              </Typography>
            </Alert>
          </Paper>

          {/* Quick Links */}
          <Paper sx={{ p: 4 }}>
            <Typography variant="h6" gutterBottom sx={{ fontWeight: 'bold' }}>
              Quick Links
            </Typography>
            
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
              <Chip 
                icon={<AcademicIcon />} 
                label="For Authors - Submission Guidelines" 
                variant="outlined" 
                clickable 
                sx={{ justifyContent: 'flex-start' }}
              />
              <Chip 
                icon={<SupportIcon />} 
                label="Peer Review Information" 
                variant="outlined" 
                clickable 
                sx={{ justifyContent: 'flex-start' }}
              />
              <Chip 
                icon={<DocumentIcon />} 
                label="Editorial Board" 
                variant="outlined" 
                clickable 
                sx={{ justifyContent: 'flex-start' }}
              />
            </Box>
          </Paper>
        </Box>

        {/* Contact Form */}
        <Paper sx={{ p: 4 }}>
          <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold', mb: 3 }}>
            Send us a Message
          </Typography>

          <form onSubmit={handleSubmit(onSubmit)}>
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
              {/* Name and Email */}
              <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', sm: '1fr 1fr' }, gap: 2 }}>
                <TextField
                  fullWidth
                  label="Full Name"
                  {...register('name', { 
                    required: 'Full name is required',
                    minLength: { value: 2, message: 'Name must be at least 2 characters' }
                  })}
                  error={Boolean(errors.name)}
                  helperText={errors.name?.message}
                />
                
                <TextField
                  fullWidth
                  label="Email Address"
                  type="email"
                  {...register('email', { 
                    required: 'Email is required',
                    pattern: {
                      value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                      message: 'Invalid email address'
                    }
                  })}
                  error={Boolean(errors.email)}
                  helperText={errors.email?.message}
                />
              </Box>

              {/* Organization (Optional) */}
              <TextField
                fullWidth
                label="Organization/Institution (Optional)"
                {...register('organization')}
              />

              {/* Category and Subject */}
              <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', sm: '1fr 1fr' }, gap: 2 }}>
                <FormControl fullWidth error={Boolean(errors.category)}>
                  <InputLabel>Inquiry Category</InputLabel>
                  <Select
                    label="Inquiry Category"
                    {...register('category', { required: 'Please select a category' })}
                    defaultValue=""
                  >
                    {contactCategories.map((category) => (
                      <MenuItem key={category.value} value={category.value}>
                        {category.label}
                      </MenuItem>
                    ))}
                  </Select>
                  {errors.category && (
                    <Typography variant="caption" color="error" sx={{ mt: 0.5, ml: 2 }}>
                      {errors.category.message}
                    </Typography>
                  )}
                </FormControl>

                <TextField
                  fullWidth
                  label="Subject"
                  {...register('subject', { 
                    required: 'Subject is required',
                    minLength: { value: 5, message: 'Subject must be at least 5 characters' }
                  })}
                  error={Boolean(errors.subject)}
                  helperText={errors.subject?.message}
                />
              </Box>

              {/* Message */}
              <TextField
                fullWidth
                label="Message"
                multiline
                rows={6}
                {...register('message', { 
                  required: 'Message is required',
                  minLength: { value: 20, message: 'Message must be at least 20 characters' }
                })}
                error={Boolean(errors.message)}
                helperText={errors.message?.message || 'Please provide detailed information about your inquiry'}
              />

              {/* Submit Button */}
              <Button
                type="submit"
                variant="contained"
                size="large"
                disabled={isSubmitting}
                startIcon={isSubmitting ? <CircularProgress size={20} /> : <SendIcon />}
                sx={{ mt: 2 }}
              >
                {isSubmitting ? 'Sending Message...' : 'Send Message'}
              </Button>

              <Typography variant="caption" color="text.secondary" sx={{ textAlign: 'center' }}>
                We typically respond to inquiries within 24-48 hours during business days.
              </Typography>
            </Box>
          </form>
        </Paper>
      </Box>

      {/* FAQ Section */}
      <Paper sx={{ p: 4, mb: 4 }}>
        <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold', display: 'flex', alignItems: 'center', gap: 2, mb: 3 }}>
          <QuestionIcon color="primary" />
          Frequently Asked Questions
        </Typography>

        {faqs.map((faq, index) => (
          <Accordion key={index}>
            <AccordionSummary expandIcon={<ExpandMoreIcon />}>
              <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                {faq.question}
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography variant="body1" color="text.secondary">
                {faq.answer}
              </Typography>
            </AccordionDetails>
          </Accordion>
        ))}
      </Paper>

      {/* Emergency Contact */}
      <Paper sx={{ p: 4, background: 'linear-gradient(135deg, #1976d2 0%, #1565c0 100%)', color: 'white', textAlign: 'center' }}>
        <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold' }}>
          Need Immediate Assistance?
        </Typography>
        <Typography variant="body1" paragraph>
          For urgent technical issues or time-sensitive inquiries, contact us directly:
        </Typography>
        <Box sx={{ display: 'flex', justifyContent: 'center', gap: 4, flexWrap: 'wrap', mt: 3 }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <PhoneIcon />
            <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
              +91 6280713069
            </Typography>
          </Box>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <EmailIcon />
            <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
              contact@skillmate.org.in
            </Typography>
          </Box>
        </Box>
      </Paper>
    </Container>
  );
};

export default ContactPage;