import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  Paper,
  TextField,
  Button,
  Card,
  CardContent,
  FormControlLabel,
  Checkbox,
  FormGroup,

  Chip,
  Divider,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  CircularProgress,
} from '@mui/material';
import {
  Email as EmailIcon,
  CheckCircle as CheckCircleIcon,
  Notifications as NotificationsIcon,
  Science as ScienceIcon,
  TrendingUp as TrendingIcon,
  Schedule as ScheduleIcon,
  Group as CommunityIcon,
} from '@mui/icons-material';
import { useForm } from 'react-hook-form';

interface NewsletterPreferences {
  weeklyDigest: boolean;
  newIssueAlerts: boolean;
  breakingResearch: boolean;
  editorialUpdates: boolean;
  conferenceNews: boolean;
  jobAlerts: boolean;
}

interface FormData {
  email: string;
  firstName: string;
  lastName: string;
  institution: string;
  interests: string[];
}

const SubscribePage: React.FC = () => {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [subscriptionSuccess, setSubscriptionSuccess] = useState(false);
  const [interests, setInterests] = useState<string[]>([]);
  const [preferences, setPreferences] = useState<NewsletterPreferences>({
    weeklyDigest: true,
    newIssueAlerts: true,
    breakingResearch: false,
    editorialUpdates: false,
    conferenceNews: false,
    jobAlerts: false,
  });

  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
  } = useForm<FormData>({
    defaultValues: {
      email: '',
      firstName: '',
      lastName: '',
      institution: '',
      interests: [],
    },
  });

  const onSubmit = async (data: FormData) => {
    setIsSubmitting(true);
    
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      console.log('Newsletter subscription:', {
        ...data,
        interests,
        preferences,
      });
      
      setSubscriptionSuccess(true);
    } catch (error) {
      console.error('Subscription error:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  const researchAreas = [
    'Artificial Intelligence',
    'Machine Learning',
    'Data Science',
    'Computer Vision',
    'Natural Language Processing',
    'Robotics',
    'Cybersecurity',
    'Software Engineering',
    'Medical AI',
    'Blockchain',
    'Quantum Computing',
    'Bioinformatics',
  ];

  const handleInterestToggle = (interest: string) => {
    const newInterests = interests.includes(interest)
      ? interests.filter((i: string) => i !== interest)
      : [...interests, interest];
    
    setInterests(newInterests);
    setValue('interests', newInterests);
  };

  const handlePreferenceChange = (preference: keyof NewsletterPreferences) => {
    setPreferences(prev => ({
      ...prev,
      [preference]: !prev[preference],
    }));
  };

  const benefits = [
    {
      icon: <ScienceIcon color="primary" />,
      title: 'Latest Research',
      description: 'Stay updated with cutting-edge research findings and breakthrough discoveries',
    },
    {
      icon: <TrendingIcon color="primary" />,
      title: 'Trending Topics',
      description: 'Get insights into emerging trends and hot topics in your field of interest',
    },
    {
      icon: <ScheduleIcon color="primary" />,
      title: 'Early Access',
      description: 'Be the first to know about new journal issues and important announcements',
    },
    {
      icon: <CommunityIcon color="primary" />,
      title: 'Research Community',
      description: 'Connect with fellow researchers and join our growing academic community',
    },
  ];

  if (subscriptionSuccess) {
    return (
      <Container maxWidth="md" sx={{ py: 6 }}>
        <Paper sx={{ p: 6, textAlign: 'center' }}>
          <CheckCircleIcon sx={{ fontSize: 80, color: 'success.main', mb: 3 }} />
          <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
            Welcome to Skillmate Newsletter!
          </Typography>
          <Typography variant="body1" paragraph color="text.secondary">
            Thank you for subscribing to our newsletter. You'll receive a confirmation email shortly.
          </Typography>
          <Typography variant="body2" color="text.secondary" paragraph>
            Your first newsletter will arrive in your inbox within the next week.
          </Typography>
          <Button
            variant="contained"
            size="large"
            onClick={() => window.location.href = '/'}
            sx={{ mt: 2 }}
          >
            Return to Homepage
          </Button>
        </Paper>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      {/* Header Section */}
      <Box sx={{ textAlign: 'center', mb: 6 }}>
        <Typography variant="h3" gutterBottom sx={{ fontWeight: 'bold' }}>
          Stay Connected with Research
        </Typography>
        <Typography variant="h6" color="text.secondary" paragraph>
          Subscribe to our newsletter and never miss important research updates
        </Typography>
      </Box>

      <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: '1fr 1fr' }, gap: 4 }}>
        {/* Benefits Section */}
        <Box>
          <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold', mb: 3 }}>
            Why Subscribe?
          </Typography>
          
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3, mb: 4 }}>
            {benefits.map((benefit, index) => (
              <Card key={index} variant="outlined">
                <CardContent sx={{ display: 'flex', gap: 2 }}>
                  <Box sx={{ flexShrink: 0 }}>
                    {benefit.icon}
                  </Box>
                  <Box>
                    <Typography variant="h6" gutterBottom sx={{ fontWeight: 'bold' }}>
                      {benefit.title}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {benefit.description}
                    </Typography>
                  </Box>
                </CardContent>
              </Card>
            ))}
          </Box>

          {/* Newsletter Preview */}
          <Card sx={{ backgroundColor: 'grey.50', border: '2px dashed', borderColor: 'grey.300' }}>
            <CardContent>
              <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <EmailIcon color="primary" />
                Newsletter Preview
              </Typography>
              <Typography variant="body2" color="text.secondary" paragraph>
                Our weekly digest includes:
              </Typography>
              <List dense>
                <ListItem>
                  <ListItemIcon>
                    <CheckCircleIcon fontSize="small" color="success" />
                  </ListItemIcon>
                  <ListItemText primary="Top 5 research articles of the week" />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <CheckCircleIcon fontSize="small" color="success" />
                  </ListItemIcon>
                  <ListItemText primary="Editor's picks and featured content" />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <CheckCircleIcon fontSize="small" color="success" />
                  </ListItemIcon>
                  <ListItemText primary="Upcoming conferences and events" />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <CheckCircleIcon fontSize="small" color="success" />
                  </ListItemIcon>
                  <ListItemText primary="Research community spotlights" />
                </ListItem>
              </List>
            </CardContent>
          </Card>
        </Box>

        {/* Subscription Form */}
        <Paper sx={{ p: 4 }}>
          <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold', mb: 3 }}>
            Subscribe Now
          </Typography>

          <form onSubmit={handleSubmit(onSubmit)}>
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
              {/* Personal Information */}
              <Box>
                <Typography variant="h6" gutterBottom sx={{ fontWeight: 'bold' }}>
                  Personal Information
                </Typography>
                
                <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
                  <TextField
                    fullWidth
                    label="First Name"
                    {...register('firstName', { 
                      required: 'First name is required',
                      minLength: { value: 2, message: 'First name must be at least 2 characters' }
                    })}
                    error={Boolean(errors.firstName)}
                    helperText={errors.firstName?.message}
                  />
                  <TextField
                    fullWidth
                    label="Last Name"
                    {...register('lastName', { 
                      required: 'Last name is required',
                      minLength: { value: 2, message: 'Last name must be at least 2 characters' }
                    })}
                    error={Boolean(errors.lastName)}
                    helperText={errors.lastName?.message}
                  />
                </Box>

                <TextField
                  fullWidth
                  label="Email Address"
                  type="email"
                  {...register('email', { 
                    required: 'Email is required',
                    pattern: {
                      value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                      message: 'Invalid email address'
                    }
                  })}
                  error={Boolean(errors.email)}
                  helperText={errors.email?.message}
                  sx={{ mb: 2 }}
                />

                <TextField
                  fullWidth
                  label="Institution/Organization (Optional)"
                  {...register('institution')}
                />
              </Box>

              <Divider />

              {/* Research Interests */}
              <Box>
                <Typography variant="h6" gutterBottom sx={{ fontWeight: 'bold' }}>
                  Research Interests (Optional)
                </Typography>
                <Typography variant="body2" color="text.secondary" paragraph>
                  Select areas you're interested in to receive personalized content:
                </Typography>
                
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                  {researchAreas.map((area) => (
                    <Chip
                      key={area}
                      label={area}
                      clickable
                      color={interests.includes(area) ? 'primary' : 'default'}
                      variant={interests.includes(area) ? 'filled' : 'outlined'}
                      onClick={() => handleInterestToggle(area)}
                    />
                  ))}
                </Box>
              </Box>

              <Divider />

              {/* Newsletter Preferences */}
              <Box>
                <Typography variant="h6" gutterBottom sx={{ fontWeight: 'bold' }}>
                  Newsletter Preferences
                </Typography>
                
                <FormGroup>
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={preferences.weeklyDigest}
                        onChange={() => handlePreferenceChange('weeklyDigest')}
                      />
                    }
                    label={
                      <Box>
                        <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                          Weekly Research Digest
                        </Typography>
                        <Typography variant="caption" color="text.secondary">
                          Curated weekly summary of top research articles
                        </Typography>
                      </Box>
                    }
                  />
                  
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={preferences.newIssueAlerts}
                        onChange={() => handlePreferenceChange('newIssueAlerts')}
                      />
                    }
                    label={
                      <Box>
                        <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                          New Issue Alerts
                        </Typography>
                        <Typography variant="caption" color="text.secondary">
                          Notifications when new journal issues are published
                        </Typography>
                      </Box>
                    }
                  />
                  
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={preferences.breakingResearch}
                        onChange={() => handlePreferenceChange('breakingResearch')}
                      />
                    }
                    label={
                      <Box>
                        <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                          Breaking Research News
                        </Typography>
                        <Typography variant="caption" color="text.secondary">
                          Immediate updates on groundbreaking discoveries
                        </Typography>
                      </Box>
                    }
                  />
                  
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={preferences.editorialUpdates}
                        onChange={() => handlePreferenceChange('editorialUpdates')}
                      />
                    }
                    label={
                      <Box>
                        <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                          Editorial Updates
                        </Typography>
                        <Typography variant="caption" color="text.secondary">
                          Messages from editors and journal announcements
                        </Typography>
                      </Box>
                    }
                  />
                </FormGroup>
              </Box>

              {/* Submit Button */}
              <Button
                type="submit"
                variant="contained"
                size="large"
                disabled={isSubmitting}
                startIcon={isSubmitting ? <CircularProgress size={20} /> : <NotificationsIcon />}
                sx={{ mt: 2 }}
              >
                {isSubmitting ? 'Subscribing...' : 'Subscribe to Newsletter'}
              </Button>

              <Typography variant="caption" color="text.secondary" sx={{ textAlign: 'center' }}>
                By subscribing, you agree to receive newsletters from Skillmate Journal.
                You can unsubscribe at any time by clicking the link in our emails.
              </Typography>
            </Box>
          </form>
        </Paper>
      </Box>

      {/* Social Proof */}
      <Paper sx={{ p: 4, mt: 6, textAlign: 'center', backgroundColor: 'primary.main', color: 'white' }}>
        <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold' }}>
          Join 50,000+ Researchers
        </Typography>
        <Typography variant="body1">
          Trusted by researchers from leading institutions worldwide
        </Typography>
        <Box sx={{ display: 'flex', justifyContent: 'center', gap: 4, mt: 3, flexWrap: 'wrap' }}>
          <Typography variant="body2" sx={{ opacity: 0.9 }}>
            Stanford • MIT • Harvard • Oxford • Cambridge
          </Typography>
        </Box>
      </Paper>
    </Container>
  );
};

export default SubscribePage;