import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Journal, Volume, Issue } from '../../types';

interface JournalsState {
  journals: Journal[];
  currentJournal: Journal | null;
  volumes: Volume[];
  currentVolume: Volume | null;
  issues: Issue[];
  currentIssue: Issue | null;
  loading: boolean;
  error: string | null;
}

const initialState: JournalsState = {
  journals: [],
  currentJournal: null,
  volumes: [],
  currentVolume: null,
  issues: [],
  currentIssue: null,
  loading: false,
  error: null,
};

const journalsSlice = createSlice({
  name: 'journals',
  initialState,
  reducers: {
    fetchJournalsStart: (state) => {
      state.loading = true;
      state.error = null;
    },
    fetchJournalsSuccess: (state, action: PayloadAction<Journal[]>) => {
      state.loading = false;
      state.journals = action.payload;
      state.error = null;
    },
    fetchJournalsFailure: (state, action: PayloadAction<string>) => {
      state.loading = false;
      state.error = action.payload;
    },
    setCurrentJournal: (state, action: PayloadAction<Journal>) => {
      state.currentJournal = action.payload;
    },
    setVolumes: (state, action: PayloadAction<Volume[]>) => {
      state.volumes = action.payload;
    },
    setCurrentVolume: (state, action: PayloadAction<Volume>) => {
      state.currentVolume = action.payload;
    },
    setIssues: (state, action: PayloadAction<Issue[]>) => {
      state.issues = action.payload;
    },
    setCurrentIssue: (state, action: PayloadAction<Issue>) => {
      state.currentIssue = action.payload;
    },
    clearJournalNavigation: (state) => {
      state.currentJournal = null;
      state.volumes = [];
      state.currentVolume = null;
      state.issues = [];
      state.currentIssue = null;
    },
  },
});

export const {
  fetchJournalsStart,
  fetchJournalsSuccess,
  fetchJournalsFailure,
  setCurrentJournal,
  setVolumes,
  setCurrentVolume,
  setIssues,
  setCurrentIssue,
  clearJournalNavigation,
} = journalsSlice.actions;

export default journalsSlice.reducer;