import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { Provider } from 'react-redux';
import { store } from './store';
import Layout from './components/layout/Layout';
import Homepage from './pages/Homepage';
import LoginPage from './pages/LoginPage';
import RegisterPage from './pages/RegisterPage';
import JournalsPage from './pages/JournalsPage';
import ArticlePage from './pages/ArticlePage';
import SearchPage from './pages/SearchPage';
import ArticleListingPage from './pages/ArticleListingPage';
import SubscribePage from './pages/SubscribePage';
import AboutPage from './pages/AboutPage';
import ContactPage from './pages/ContactPage';

// Placeholder components for other pages
const ResetPasswordPage = () => <div>Reset Password Page - Coming Soon</div>;

function App() {
  return (
    <Provider store={store}>
      <Router>
        <Layout>
          <Routes>
            <Route path="/" element={<Homepage />} />
            <Route path="/login" element={<LoginPage />} />
            <Route path="/register" element={<RegisterPage />} />
            <Route path="/reset-password" element={<ResetPasswordPage />} />
            <Route path="/journals" element={<JournalsPage />} />
            <Route path="/journals/:journalId" element={<JournalsPage />} />
            <Route path="/journals/:journalId/:year" element={<JournalsPage />} />
            <Route path="/journals/:journalId/:year/:volume" element={<JournalsPage />} />
            <Route path="/journals/:journalId/:year/:volume/:issue" element={<ArticleListingPage />} />
            <Route path="/articles/:articleId" element={<ArticlePage />} />
            <Route path="/search" element={<SearchPage />} />
            <Route path="/subscribe" element={<SubscribePage />} />
            <Route path="/manage-subscription" element={<SubscribePage />} />
            <Route path="/about" element={<AboutPage />} />
            <Route path="/contact" element={<ContactPage />} />
            <Route path="/for-authors" element={<div>For Authors - Coming Soon</div>} />
            <Route path="/editorial-board" element={<div>Editorial Board - Coming Soon</div>} />
            <Route path="/submission-guidelines" element={<div>Submission Guidelines - Coming Soon</div>} />
            <Route path="/privacy-policy" element={<div>Privacy Policy - Coming Soon</div>} />
            <Route path="/terms-of-service" element={<div>Terms of Service - Coming Soon</div>} />
            <Route path="/cookie-policy" element={<div>Cookie Policy - Coming Soon</div>} />
            <Route path="*" element={<div>404 - Page Not Found</div>} />
          </Routes>
        </Layout>
      </Router>
    </Provider>
  );
}

export default App;
